import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
export default {
    title: 'Payment Method',
    icon: 'mdi-credit-card-settings-outline',
    items: {
        buttons: tableButtons.get('new', 'delete'),
        headers: [
            {
                text: 'Name',
                align: 'start',

                value: 'name',
            },
            { text: 'Type', value: 'type', width: '20%' },
            { text: 'Active', value: 'active', sortable: false, width: '10%' },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() }
        ],
    },
    
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'select',
                    name: 'type',
                    bind: {
                        label: "Type",
                        rules: [
                            v => !!v || 'Type is required',
                        ],
                        items: [
                            {
                                label: "Credit Card",
                                value: "cc"
                            },
                            {
                                label: "Wire",
                                value: "wire"
                            }
                        ],
                        itemText: "label",
                        itemValue: "value",
                        required: true,
                    }
                },
                {
                    input: 'switch',
                    name: 'active',
                    default: true,
                    bind: {
                        label: "Active"
                    }
                }
            ]
        }
    ]
}