

import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'

export default {
    title: 'Bank Transactions',
    icon: 'mdi-bank-transfer',
     
    
    items: {
        total: {
            headers: [
                {
                    text: 'Currency',
                    align: 'start',
                    value: '_id',
                    component: () => import('./../components/items/fields/Currency.vue'),
                    sortable: false,
                    width:"50"
                },
                {
                    text: 'Subtotal',
                    align: 'start',
                    value: 'subtotal.amount',
                    format: 'money',
                    formatArgs: ['$_id', true],
                    sortable: false,
                },
                {
                    text: 'Withraw',
                    align: 'start',
                    format: 'money',
                    formatArgs: ['$_id', true],
                    value: 'withdraw.amount',
                    sortable: false,
                },
                {
                    text: 'Total',
                    align: 'start',
                    format: 'money',
                    formatArgs: ['$_id', true],
                    value: 'amount',
                    sortable: false,
                },
                // {
                //     text: 'Total SAR',
                //     align: 'start',
                //     format: 'money',
                //     formatArgs: ['SAR', true],
                //     value: 'amount_sar',
                //     sortable: false,
                // }
            ]
        },
        buttons: tableButtons.get('new', 'withdraw', 'delete'),
        filters: [


            {
                input: 'autocomplete',
                name: 'bank',
                bind: {
                    label: "Bank",
                    prependInnerIcon: "mdi-bank",
                    collection: 'bank',
                    itemText: "number",
                    itemValue: "_id",
                    hideAddNew: true,
                }
            },
            {
                input: 'range',
                name: 'created_on',
                bind: {
                    label: "Date",
                    prependInnerIcon: "mdi-calendar",

                }
            }

        ],
        headers: [
            { text: 'Agent', value: 'agent' },
            { text: 'Bank', value: 'bank.number' },
            { text: 'Country', value: 'bank.country', format: 'country', width: '100' },
            { text: 'Currency', value: 'currency', sortable: false, component: () => import('./../components/items/fields/Currency.vue') },
            { text: 'Type', sortable: false, value: 'type', format: 'depositType', width: '50'  },
            // { text: 'Amount', value: 'amount', format: 'money', formatArgs: ['$bank.currency'] },
            { text: 'TRY', value: 'amount_try', format: 'money', formatArgs: ['TRY', true] },
            { text: 'EUR', value: 'amount_eur', format: 'money', formatArgs: ['EUR', true] },
            { text: 'SAR', value: 'amount_sar', format: 'money', formatArgs: ['SAR', true] },
            { text: 'USD', value: 'amount_usd', format: 'money', formatArgs: ['USD', true] },
            { text: 'Date', value: 'created_on', format: 'date' },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() }

        ]
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'agent',
                    bind: {
                        label: "Agent",
                        prependIcon: 'mdi-account',
                        required: true,
                        rules: [
                            v => !!v || 'Agent is required',
                        ],
                    }
                },
                {
                    input: 'autocomplete',
                    name: 'bank',
                    bind: {
                        label: "Bank",
                        prependIcon: "mdi-bank",
                        collection: 'bank',
                        itemText: "number",
                        itemValue: "_id",
                        required: true,
                        searchFields: ['number', 'country'],
                       
                        rules: [
                            v => !!v || 'Bank is required',
                        ],
                    },
                    on: (vm) => ({
                        data (bank) {
                            
                            vm.$refs.banktransaction_currency[0].$emit('input', bank ? bank.currency : '')
                        }
                    })
                },
                {
                    input: 'select',
                    name: 'currency',
                    bind: {
                        label: "Currency",
                        prependIcon: "mdi-currency-usd-circle-outline",
                        items: ['USD', 'EUR', 'SAR', 'TRY'],
                        required: true,
                        rules: [
                            v => !!v || 'Currency is required',
                        ],
                    },
                    on: (vm) => ({
                        input(currency) {
                            console.log(currency);
                            const icons = {
                                'USD': 'mdi-currency-usd',
                                'SAR': 'mdi-currency-rial',
                                'TRY': 'mdi-currency-try',
                                'EUR': 'mdi-currency-eur',
                            }

                            if (currency) {
                                vm.props['banktransaction_amount'] = {
                                    prependIcon: icons[currency],
                                    label: 'Amount in ' + currency
                                }

                            } else if (vm.props['banktransaction_amount'])
                                delete vm.props['banktransaction_amount']
                        }
                    })
                },
                {
                    input: 'text',
                    name: 'amount',
                    bind: (vm) => ({
                        label: (() => {
                            console.log(vm.data.currency);
                             
                            return vm.data.currency ? `Amount in ${vm.data.currency}` : "Amount"
                        })(),
                        prependIcon: (() => {
                            console.log(vm.data.currency);
                            const icons = {
                                'USD': 'mdi-currency-usd',
                                'SAR': 'mdi-currency-rial',
                                'TRY': 'mdi-currency-try',
                                'EUR': 'mdi-currency-eur',
                            }
                            return icons[vm.data.currency] || 'mdi-cash'
                        })(),
                        required: true,
                        type: 'number',
                        rules: [
                            v => !!v || 'Amount is required',
                        ],
                    })
                },
                {
                    input: 'text',
                    name: 'amount_sar',

                    bind: {
                        label: "Amount in SAR",
                        prependIcon: 'mdi-currency-rial',
                        required: true,
                        type: 'number',
                        rules: [
                            v => !!v || 'Amount is required',
                        ],
                    }
                },
             
                {
                    input: 'hidden',
                    name: 'type',
                    default () {
                        const urlParams = new URLSearchParams(location.search);
                        return urlParams.get('type') || 'deposit'
                    }
                },
                {
                    input: 'date',
                    name: 'created_on',
                    default: (new Date()).toISOString().split('T')[0],
                    bind: {
                        label: "Created Date",
                        prependIcon: 'mdi-calendar',
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'Created On is required',
                        ],
                    }
                },

                 
                {
                    input: 'autocomplete',
                    name: 'user',
                    if: state => (state.auth.user.rights.filter(e => e.collection == 'transaction')[0].write.allOwn == 'all'),
                    default: state => state.auth.user._id,
                    bind: {
                        label: "User",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        required: true,
                        or: false,
                        query: {
                            role: ['!wakel', '!affiliate'],
                        },
                        rules: [
                            v => !!v || 'User is required',
                        ],
                    }
                },
                
               
            ]
        }
    ]
}