

import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
function pad(number, length) {

    var str = '' + number;
    while (str.length < length) {
        str = '0' + str;
    }

    return str;

}

export default {
    title: 'Transactions',
    icon: 'mdi-bank-transfer',
    withdraw: true,
    currency: 'SAR',
    total: true,
   
    items: {
        total: {
            headers: [
                {
                    text: 'Subtotal',
                    align: 'start',
                    value: 'subtotal.amount',
                    format: 'money',
                    sortable: false,
                },
                {
                    text: 'Withraw',
                    align: 'start',
                    format: 'money',
                    value: 'withdraw.amount',
                    sortable: false,
                },
                {
                    text: 'Total',
                    align: 'start',
                    format: 'money',
                    value: 'amount',
                    sortable: false,
                }
            ]
        },
        buttons: tableButtons.get('new', 'withdraw', 'delete'),
        filters: [



            {
                input: 'autocomplete',
                name: 'user',
                if: state => {
                    console.log('asdsad => ', state.auth.user.rights);
                    return (state.auth.user.rights.filter(e => e.collection == 'transaction')[0].write.allOwn == 'all')
                },
                bind: {
                    label: "Wakel",
                    prependInnerIcon: "mdi-account",
                    collection: 'user',
                    itemText: "displayName",
                    itemValue: "_id",
                    hideAddNew: true,
                    or: false,
                    query: {
                        role: 'wakel'
                    },

                }
            },

            {
                input: 'range',
                name: 'created_on',
                default: () => {
                    const today = new Date()
                    today.setHours(0, 0, 0, 0);
                    // console.log([`${today.getFullYear()}-${pad(today.getMonth() + 1, 2)}-01`, `${today.getFullYear()}-${pad(today.getMonth() + 1, 2)}-${pad(today.getDate(), 2)}`]);
                    return [`${today.getFullYear()}-${pad(today.getMonth() + 1, 2)}-01`, `${today.getFullYear()}-${pad(today.getMonth() + 1, 2)}-${pad(today.getDate(), 2)}`]
                },
                bind: {
                    label: "Date",
                    prependInnerIcon: "mdi-calendar",

                }
            }

        ],
        headers: [
            { text: 'Account name', value: 'name', class: "font-weight-bold" },
            { text: 'Type', sortable: false, value: 'type', format: 'depositType', width: '50'  },
            { text: 'Amount', value: 'amount', format: 'money' },
            // { text: 'Email', value: 'email' },
            { text: 'Date', value: 'created_on', format: 'date' },
            { 
                text: 'Label', 
                sortable: false, 
                value: 'user.displayName',
                width: '200',
                component: () => import('./../components/items/fields/User.vue')
            },
            { text: 'profileImage', sortable: false, value: 'user.profileImage', hide: true },
            { text: '', value: 'actions', sortable: false, width: '150', buttons: rowButtons.get() }
        ]
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Account name",
                        prependIcon: "mdi-account-box",
                     
                    }
                },
                {
                    input: 'text',
                    name: 'amount',
                   
                    bind: {
                        label: "Amount",
                        prependIcon: 'mdi-cash',
                        required: true,
                        type: 'number',
                        rules: [
                            v => !!v || 'Amount is required',
                        ],
                    }
                },
               
             
                {
                    input: 'hidden',
                    name: 'type',
                    default () {
                        const urlParams = new URLSearchParams(location.search);
                        return urlParams.get('type') || 'deposit'
                    }
                },
                {
                    input: 'date',
                    name: 'created_on',
                    default: (new Date()).toISOString().split('T')[0],
                    bind: {
                        label: "Created Date",
                        prependIcon: 'mdi-calendar',
                        required: true,
                        
                        
                        rules: [
                            v => !!v || 'Created On is required',
                        ],
                    }
                },

                 
                {
                    input: 'autocomplete',
                    name: 'user',
                    if: state => (state.auth.user.rights.filter(e => e.collection == 'transaction')[0].write.allOwn == 'all'),
                    bind: {
                        label: "Label",
                        prependIcon: "mdi-account",
                        collection: 'user',
                        itemText: "displayName",
                        itemValue: "_id",
                        required: true,
                        or: false,
                        query: {
                            role: 'wakel'
                        },
                        rules: [
                            v => !!v || 'Label is required',
                        ],
                    }
                },
                
               
            ]
        }
    ]
}