import tableButtons from './defaults/items_buttons'
import rowButtons from './defaults/row_buttons'
export default {
    title: 'Department',
    icon: 'mdi-briefcase-account-outline',
    items: {
        buttons: tableButtons.get('new', 'delete'),
        headers: [
            {
                text: 'Name',
                align: 'start',
                sortable: false,
                value: 'name',
            },
            {
                text: 'Deposit Type',
                sortable: false,
                value: 'type',
            },
            { text: '', value: 'actions', width: '150', buttons: rowButtons.get() }
        ]
    },
    form: [
        {
            label: 'General',
            fields: [
                {
                    input: 'text',
                    name: 'name',
                    bind: {
                        label: "Name",
                        rules: [
                            v => !!v || 'Name is required',
                        ],
                        required: true,
                    }
                },
                {
                    input: 'select',
                    name: 'type',
                    bind: {
                        label: "Deposit Type",
                        items: [
                            { text: 'FTD', value: 'ftd' },
                            { text: 'Retention', value: 'rtn' },
                            { text: 'Withdraw', value: 'wtd' },
                        ],
                        rules: [
                            v => !!v || 'Deposit Type',
                        ],
                        required: true,
                    }
                }
            ]
        }
    ]
}