export default {
    get () {
        const args = [...arguments]

        const buttons = args.map(e => {
            return e.toLowerCase() == 'spacer' ? { spacer: true } : this.buttons.find(el => el.title.toLowerCase() == e.toLowerCase())
        }).filter(e => e)

        return buttons.length ? buttons : this.buttons
    },
    buttons: [
        {
            title: 'Edit',
            icon: 'mdi-pencil',
            bind: {
                color: 'info'
            },
            on: (vm, item) => ({
                click () {
                    vm.$router.push(vm.$route.path + '/' + item._id)
                }
            })
        },
        {
            title: 'Delete',
            icon: 'mdi-delete',
            bind: {
                color: 'red'
            },
            on: (vm, item) => ({
                async click() {
                    const confirmed = await vm.$confirm('Are you sure do you want to delete this item ?', { title: 'Delete Item', color: 'red' })
                    if (confirmed) {
                        vm.loading = true
                        const rm = await vm.remove({ collection: vm.$route.params.collection, id: item._id })
                        if (rm.status === 200) {

                            vm.fetch()
                            vm.loading = false
                            vm.$store.commit('success', 'Item has been deleted successfuly')
                        }
                    }
                }
            })
        },
        {
            title: 'Duplicate',
            icon: 'mdi-content-copy',
            on: (vm, item) => ({
                click() {
                    vm.$router.push(vm.$route.path + '/new?clone=' + item._id)
                }
            })
        }
    ]
}